.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0, 0.5);
  z-index: 20;
}
.popup_inner {
  position: absolute;
  left: 25%;
  right: 25%;
  top: 25%;
  bottom: 25%;
  margin: auto;
  background: white;
  overflow: auto;
}
@media (max-width: 575.98px) {
  .popup_inner{
    position: absolute;
    left: 0%;
    right: 0%;
    top: 0%;
    bottom: 0%;
    overflow: auto;
  }
}
@media (min-width: 576px) and (max-width: 767.98px) {
  .popup_inner{
    position: absolute;
    left: 15%;
    right: 15%;
    top: 15%;
    bottom: 15%;
    overflow: auto;
  }
}
